/*
Button Flip
*/
.tf-btn-flip {
  opacity: 1;
  outline: 0;
  color: #ffffff;
  position: relative;
  text-align: center;
  letter-spacing: 1px;
  display: inline-block;
  text-decoration: none;
}
.tf-btn-flip:hover:after {
  opacity: 1;
  transform: translateY(0) rotateX(0);
}
.tf-btn-flip:hover:before {
  opacity: 0;
  transform: translateY(50%) rotateX(90deg);
}
.tf-btn-flip:after {
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  color: #ffffff;
  display: block;
  transition: 0.5s;
  position: absolute;
  background: var(--tf-primary);
  content: attr(data-back);
  transform: translateY(-50%) rotateX(90deg);
  border-radius: 4px;
  padding: 6px 20px;
}
.tf-btn-flip:before {
  top: 0;
  left: 0;
  opacity: 1;
  color: #ffffff;
  display: block;
  transition: 0.5s;
  position: relative;
  background: var(--tf-primary);
  content: attr(data-front);
  transform: translateY(0) rotateX(0);
  border-radius: 4px;
  padding: 6px 20px;
}

/*
Modal
*/
.tf-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: scale(0.7);
  -ms-transform: scale(0.7);
  -o-transform: scale(0.7);
  transform: scale(0.7);
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.tf-modal-show {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.tf-modal .tf-modal-dialog {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  min-height: 100%;
  max-width: 100%;
  position: relative;
  pointer-events: none;
  margin: 0;
}
.tf-modal .tf-modal-content {
  position: relative;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  max-width: 1080px;
  pointer-events: auto;
  background-color: #ffffff;
  outline: 0;
  margin: 0 auto;
  border-radius: 10px;
  padding: 30px;
  overflow: hidden;
}
.tf-modal .tf-modal-close {
  color: var(--tf-primary);
  cursor: pointer;
  font-size: 1.375rem;
  position: absolute;
  top: 12px;
  right: 16px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.tf-modal-small .tf-modal-content {
  width: 50%;
}
.tf-modal-extra-small .tf-modal-content {
  width: 30%;
  max-width: 480px;
}
@media only screen and (max-width: 1366px) {
  .tf-modal-extra-small .tf-modal-content {
    width: 40%;
  }
}
@media only screen and (max-width: 1024px) {
  .tf-modal-extra-small .tf-modal-content {
    width: 50%;
  }
}
.tf-modal-large .tf-modal-content {
  width: 80%;
}
.tf-modal-fullscreen .tf-modal-dialog {
  height: 100vh;
}
.tf-modal-fullscreen .tf-modal-content {
  width: 100%;
  max-width: 100%;
  overflow-y: auto;
  border-radius: 0;
}

body.tf-modal-open {
  overflow: hidden;
}
body.tf-modal-open .tf-modal {
  overflow-x: hidden;
  overflow-y: auto;
}
body.tf-modal-open .tf-modal.tf-modal-fullscreen {
  overflow: hidden;
}
body.tf-modal-open:after {
  content: "";
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
  background: rgba(0, 0, 0, 0.4);
}

/*
Ajax Loader
*/
.tf-btn-loading {
  padding-right: 40px;
  position: relative;
}
.tf-btn-loading:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  margin-top: -7px;
  display: inline-block;
  width: 12px;
  height: 12px;
  border: 2px solid #ffffff;
  border-left-color: transparent;
  border-radius: 50%;
  vertical-align: middle;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.2s ease, visibility 0.2s ease;
  -webkit-animation: tf-rotate 450ms infinite linear;
  animation: tf-rotate 450ms infinite linear;
}

#tour_room_details_loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  display: none;
  z-index: 99999999;
}
#tour_room_details_loader #tour-room-details-loader-img {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes tf-rotate {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes tf-rotate {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
