/* 
Tooltip
*/
.tf-tooltip {
  position: relative;
}
.tf-tooltip .tf-top {
  top: -20px;
  left: 50%;
  transform: translate(-50%, -74%);
  padding: 10px 10px;
  color: #ffffff;
  background-color: var(--tf-text-paragraph);
  font-weight: normal;
  font-size: 0.75rem;
  border-radius: 4px;
  position: absolute;
  z-index: 99999999;
  box-sizing: border-box;
  border: 1px solid var(--tf-text-paragraph);
  box-shadow: 0 1px 8px transparent;
  visibility: hidden;
  opacity: 0;
  text-align: center;
  line-height: 1;
  white-space: nowrap;
}
.tf-tooltip .tf-top i.tool-i {
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -12px;
  width: 24px;
  height: 12px;
  overflow: hidden;
}
.tf-tooltip .tf-top i.tool-i::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background-color: var(--tf-text-paragraph);
  border: 1px solid var(--tf-text-paragraph);
  box-shadow: 0 1px 8px transparent;
}
.tf-tooltip:hover .tf-top {
  visibility: visible;
  opacity: 1;
}

